@import url(https://fonts.googleapis.com/css?family=Dax:400,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ChatInput-form {
	display: flex;
}

.ChatInput-form input {
	flex: 1 1;
	height: 2rem;
}
.msg-userName{
	font-weight: bold;
	margin-top: 1rem;
	margin-bottom: 0.5rem;
	margin-left: 0.5rem;
}

.msgLeft{
	text-align: left;
	margin-bottom: 0.5rem;
}

.msgRight{
	text-align: right;
	margin: 1rem;
}

.msg-msg{
	color: white;
	font-weight: bold;
	font-size: 1rem;
	border-radius: 10px;
	padding: 0.5rem;
	margin-left: 0.5rem;
	margin-right: 0.2rem;
	margin-top: 0.5rem;
	background-color: rgb(109, 86, 24);
}


.msg-timeStamp{
	font-weight: bold;
	font-size: 0.5rem;
}


.sysMsg{
	text-align: center;
}
#userList{
	display: flex;
	flex-direction: row;
	margin-top: 1rem;
	margin-left: 0.5rem;
}

.userIcon{
	flex: 1 9;
}

.icon{
	background-color: white;
	margin: 0.2rem;
	border-radius: 20px 20px 20px 20px;

}

.userInfo{
	flex: 9 1;
}

.id{
	font-size: 1rem;
	color: white;
	font-weight: bold;
	margin-top: 0.3rem;
}

.room{
	font-size: 1rem;
	color: green;
	font-weight: bold;
}
#Chat-container{
	display: flex;
	flex-direction: row;
	height:100vh;
}

.Chat-box{
	border: 1px solid black;
}

.ChatLog{
	flex: 7 3;
	overflow-y: scroll;
	background-color: rgb(243, 188, 35);
	display: flex;
	flex-direction: column;
}

.ChatLog-top{
	flex: 1 1;
}

.ChatInput{
	flex: 0 1;
	width: 100%;
}

.UserList{
	flex: 3 7;
	max-width: 12rem;
	background-color: rgb(49, 45, 43);
	overflow-y: scroll;
	border-right: solid 1rem;
	border-color: rgb(49, 45, 43);
}



.Home-container {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.icon-container{
	margin: 1rem;
	text-align: center;
	display: flex;
	flex-direction: row;
	border-radius: 8px 8px 8px 8px;
	background-color: lightgray;
}

.icon{
	margin: 0.5rem;
	display: flex;
	flex-direction: column;
	background-color: lightgray;
}

.icon-input{
	flex: 2 8;
	width: 100%;
	margin-bottom: 0.5rem;
}

.icon-img{
	flex: 8 2;
}

.Join-button {
	margin-top: 1rem;
}
