.MakeRoomModal {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.6);
}
.MakeRoomModal button {
    outline: none;
    cursor: pointer;
    border: 0;
}
.MakeRoomModal > section {
    width: 90%;
    max-width: 450px;
    margin:0 auto;
    border-radius: .3rem;
    background-color: #fff;
    /* 팝업이 열릴때 스르륵 열리는 효과 */
    animation: MakeRoomModal-show .3s;
    overflow: hidden;
}
.MakeRoomModal > section > header {
    position: relative;
    padding: 16px 64px 16px 16px;
    background-color: #f1f1f1;
    font-weight: 700;
}
.MakeRoomModal > section > header button {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 30px;
    font-size: 21px;
    font-weight: 700;
    text-align: center;
    color: #999;
    background-color: transparent;
}
.MakeRoomModal > section > main {
    padding: 16px;
    border-bottom: 1px solid #dee2e6;
    border-top: 1px solid #dee2e6;
}
.MakeRoomModal > section > footer {
    padding: 12px 16px;
    text-align: right;
}
.MakeRoomModal > section > footer button {
    padding: 6px 12px;
    color: #fff;
    background-color: #6c757d;
    border-radius: 5px;
    font-size: 13px;
}
.MakeRoomModal.openMakeRoomModal {
    display: flex;
    align-items: center;
    /* 팝업이 열릴때 스르륵 열리는 효과 */
    animation: MakeRoomModal-bg-show .3s;
}
@keyframes MakeRoomModal-show {
    from {
        opacity: 0;
        margin-top: -50px;
    }
    to {
        opacity: 1;
        margin-top: 0;
    }
}
@keyframes MakeRoomModal-bg-show {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
